.Sidebar {
  padding: 24px 18px;
}

.Sidebar img {
  width: auto;
  margin: 0 auto;
}

.Sidebar ul {
  padding: 0;
  margin: 0;
}

.Sidebar ul li {
  list-style: none;
  margin-bottom: 12px;
}

.Sidebar a {
  color: rgba(255, 255, 255, 0.8);
  text-decoration: none;
  -webkit-transition: color 150ms ease-in;
  -o-transition: color 150ms ease-in;
  transition: color 150ms ease-in;
  cursor: pointer;
}

.Sidebar a.is-selected {
  color: white;
}

.Sidebar a:hover {
  color: white;
}

.Sidebar-header {
  margin-bottom: 24px;
}


